@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap");
:root {
  --soft-blue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  --very-dark-blue-main: hsl(217, 54%, 11%);
  --very-dark-blue-card: hsl(216, 50%, 16%);
  --very-dark-blue-line: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  font-family: "Outfit", sans-serif;
  font-weight: 500;
  background-color: var(--very-dark-blue-main);
  color: var(--soft-blue);
  padding: 0.09375rem;
  overflow: hidden;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

img {
  max-width: 100%;
  display: block;
}

.main {
  background-color: var(--very-dark-blue-card);
  border-radius: 0.3125rem;
  padding: 1.5rem;
  max-width: auto;
}
@media (min-width: 23.4375rem) {
  .main {
    width: 21.875rem;
    margin: auto;
  }
}
.main .img {
  max-width: 100%;
  max-height: 18.75rem;
  border-radius: 0.3125rem;
  margin-bottom: 1.875rem;
}
.main h1 {
  color: var(--white);
  margin-bottom: 1.5625rem;
  font-size: 1.375rem;
  font-weight: 600;
}
.main p {
  font-size: 1.125rem;
  margin-bottom: 1.5rem;
}
.main_detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.main_detail_price {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--cyan);
}
.main_detail_price img {
  margin-right: 0.375rem;
}
.main_detail_time {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main_detail_time img {
  margin-right: 0.375rem;
}
.main hr {
  border: none;
  border-top: 0.0625rem solid var(--very-dark-blue-line);
  width: 18.75rem;
}
.main_profile {
  display: flex;
  align-items: center;
  margin-top: 1.5rem;
  width: 18.75rem;
}
.main_profile_avatar {
  width: 2rem;
  height: 2rem;
  border: 0.125rem solid var(--white);
  border-radius: 50%;
  margin-right: 1rem;
}
.main_profile_avatar img {
  max-width: 100%;
  max-height: 100%;
}
.main_profile span {
  color: var(--white);
}